import { Component } from '@angular/core';
import { Platform } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { CopaAmericaPage } from '../pages/copa-america/copa-america';
import { SQLite } from '@ionic-native/sqlite';
import { DatabaseProvider } from '../providers/database/database';
import { Match } from '../pages/Match';

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  rootPage:any = "CopaAmericaPage";

  constructor(platform: Platform, 
    statusBar: StatusBar, 
    splashScreen: SplashScreen,
    public dataService: DatabaseProvider,
    public sqlite: SQLite) {
    platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      statusBar.styleDefault();
      splashScreen.hide();
      this.createDatabase();
    });
  }

  private createDatabase(){
    this.sqlite.create({
      name: 'data.db',
      location: 'default' // the location field is required
    })
    .then((db) => {
      this.dataService.setDatabase(db);
      this.dataService.createTableMatch();
      this.dataService.createTableTeam();
      this.dataService.createTableTeamDetail();

      let imageA = "assets/imgs/Flags/colombia.png";
      let imageB = "assets/imgs/Flags/brazil.png";

      let newMatch = new Match(1, 10, 11, "colombia", "brazil", "3 - 0", "2017-07-07", "FINISHED", imageA, imageB);
      //let newMatch = new Match(item.id,item.home_id, item.away_id, item.home_name, item.away_name
      //,item.score, item.date, item.status,imageA, imageB);
      this.dataService.insertMatch(newMatch);
    })
    .catch(error =>{
      console.error(error);
    });
  }
}




